/* eslint-disable eqeqeq */
import React from 'react'

import { buscarUsuarioLogado } from "../../utils/Login";
import * as permissaoUtils from "../../utils/Permissao";

function Permissao(props) {

    const RederizarComponente = ({...rest}) => {
        const permissao = permissaoUtils.buscarPermissao(rest.nome);
        const usuarioLogado = buscarUsuarioLogado();
        if(permissao){
            if(permissao.usuarios.find((item) => item == usuarioLogado.permissoes && permissao.visualizar ? true : false)){
                return <rest.componente {...props} />;
            }else{
                return <></>;
            }
        }else{
            return <rest.componente {...props} />;
        }
        
    }

    return (
        <RederizarComponente {...props} />
    )
}

export default Permissao;
