/* eslint-disable eqeqeq */
import * as backEndUtils from "./BackEnd";

export const montarUsuario = (dados) => {
    return {
        usuario: dados.usuario.valor || undefined,
        nome: dados.nome.valor || undefined,
        sobrenome: dados.sobrenome.valor || undefined,
        email: dados.email.valor,
        senha: dados.senha.valor || undefined,
        permissoes: [ dados.tipoUsuario.valor ] || [],
        criado_por: dados.criadoPor.valor || undefined,
        criado_em: dados.criadoEm.valor || undefined
    }
}

export const buscarUsuariosPaginado = async (quantidadePagina, paginaAtual) => {

    // const requisicao = {
    //     quantidadePagina : quantidadePagina,
    //     paginaAtual: paginaAtual
    // }

    return await backEndUtils.chamarBackEnd("GET", "/usuarios").then((response) => {
        if(response.status == 200) {
            return response.json().then((dados) => dados);
        }
    })
}

export const buscarUsuarioPorNome = async (nomeUsuario) => {
    return await backEndUtils.chamarBackEnd("GET", "/usuarios/" + nomeUsuario).then((response) => {
        if(response.status == 200){
            return response.json().then(dados => dados)
        }
    })
}

export const buscarUsuarioPorId = async (idUsuario) => {
    return await backEndUtils.chamarBackEnd("GET", "/usuarios/" + idUsuario).then((response) => {
        if(response.status == 200){
            return response.json().then(dados => {
                if(dados.success){
                    return dados.data;
                }else{
                    return {
                       _id: idUsuario,
                       nome: ""
                    }
                }
            })
        }
    })
}

export const cadastrarUsuario = async (usuario) => {

    return await backEndUtils.chamarBackEnd("POST", "/usuarios/", usuario).then((response) => {
        if(response.status){
            return response.json().then(dados => dados)
        }
    })
}

export const atualizarUsuario = async (usuario, id) => {

    return await backEndUtils.chamarBackEnd("PUT", "/usuarios/" + id, usuario).then((response) => {
        if(response.status){
            return response.json().then(dados => dados)
        }
    })
}

export const atualizarSenhaUsuario = async (usuario, id) => {

    return await backEndUtils.chamarBackEnd("PUT", "/usuarios/senha/" + id, usuario).then((response) => {
        if(response.status){
            return response.json().then(dados => dados)
        }
    })
}

export const deletarUsuario = async (id) => {

    return await backEndUtils.chamarBackEnd("DELETE", "/usuarios/" + id).then((response) => {
        if(response.status){
            return response.json().then(dados => dados)
        }
    })
}